<template>
    <div>
        <div class="rules__header rules__header-staff">
            <div class="text-size-ml text-bold rules__header-wrapper">
                <div class="rules__header-left">
                    <span>Сотрудники</span>
                    <div class="has-hint tumbler__staff-editable">
                        <tumbler
                            :name="tumblers.editable.name"
                            :value="tumblers.editable.value"
                            :icon="'i i-pencil2'"
                        >
                        </tumbler>
                        <span class="hint bottom-center-direction" v-if="tumblers.editable.value">Режим редактирования включен</span>
                        <span class="hint bottom-center-direction" v-else>Режим редактирования выключен</span>
                    </div>
                </div>
                <div class="rules__header-right">
                    <a href="javascript:void(0);" class="link-icon" @click="openKickUnReadAll">
                        <span class="i i-kick"></span>
                        <span class="text">пнуть всех</span>
                    </a>
                    <a href="javascript:void(0);" class="link-icon" @click="openStaffGroup">
                        <span class="i i-staff"></span>
                        <span class="text">создать группу</span>
                    </a>
                    <div class="rules__btn" @click="openAddWorker([])" >
                        <span class="elem">
                            <span class="i i-staff4"></span>
                            Добавить сотрудника
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="container staff-container">
                <div class="staff-list" :class="{'staff-list__editable': tumblers.editable.value}">
                    <div class="staff-item" v-for="(group, index) in usersGrouped" :key="'group'+group.id">
                        <div class="name">
                            {{group.name}}
                            <span @click="openStaffGroup(group)" class="i-wrapper">
                                <span class="i i-pencil2"></span>
                                <span class="i i-pencil2-active"></span>
                            </span>
                        </div>
                        <div class="change-pos">
                            <div class="change-pos__btn up" @click="up(index)">
                                <span class="i i-pointer9"></span>
                            </div>
                            <div class="change-pos__btn down" @click="down(index)">
                                <span class="i i-pointer9"></span>
                            </div>
                        </div>
                        <draggable :forceFallback="true" element="div"
                                    :value="group.users"
                                    @input="setValue($event,index)"
                                   @start="dragStart(index)"
                                   @end="dragSave(index)"
                                   :move="dragStatus"
                                   :animation="150"
                                   :touchStartThreshold="3"
                                   :delay="10"
                                   class="workers-list"
                                   v-if="group.users.length !== 0 && tumblers.editable.value"
                        >
                            <div :id="'user_'+user.id" class="worker" v-for="user in group.users" :key="user.id">
                                <tooltip
                                        v-if="!user.banned"
                                         :direction="'bottom-left-direction'"
                                         :add-class-wrap="'accesses'"
                                         :add-style="{width: 230 + 'px'}">
                                    <span slot="tooltipBtn">
                                        <div class="num">
                                            {{Object.keys(user.rights).length}}
                                        </div>
                                    </span>

                                    <div v-if="Object.keys(user.rights).length>0" slot="tooltipContent">
                                        <template v-for="sect in topSections">
                                            <div v-if="user.rights[sect.id]" class="flex-row" :key="user.id+'_'+sect.id" >
                                                <div class="text-bold text-size-s">
                                                    {{sect.name}}
                                                </div>
                                                <div class="access" :class="{editor : user.rights[sect.id]=='W', reader : user.rights[sect.id]=='R'}">
                                                    {{(user.rights[sect.id]=='W')?'редактор':'читатель'}}
                                                </div>
                                            </div>
                                        </template>
                                    </div>
                                </tooltip>
                                <div class="i i-stop i--red" v-else></div>
                                <div class="worker-name">
                                    <span class="text-size-m">{{user.name}}</span>
                                    <span class="text-color-grey text-size-xs">{{user.post}}</span>
                                </div>
                                <div class="worker-email">
                                    {{user.email}}
                                </div>
                                <div class="kick-sms">
                                    <tooltip
                                        v-if="user.mark>0 && !user.banned"
                                         :direction="'top-center-direction'"
                                         :isBtnClose="false"
                                         :add-style="{width: 184 + 'px'}">
                                        <span slot="tooltipBtn">
                                            <div class="notify-count" :class="{active: kickActive}">
                                                <span>{{user.mark}}</span>
                                            </div>
                                        </span>

                                        <div slot="tooltipContent">
                                            <div class="text-size-s mb8" >
                                                Непрочитаных правил: <span class="text-bold">{{user.mark}}</span>
                                            </div>
                                            <a href="javascript:void(0);" class="link-icon" @click="kickUnRead(user.id, $event)">
                                                <span class="standart">
                                                    <span class="i i-kick"></span>
                                                    <span class="text">пнуть по смс</span>
                                                </span>
                                                <span class="sended">
                                                    <span class="i i-ok7"></span>
                                                    <span class="text">смс отправлено</span>
                                                </span>
                                            </a>
                                        </div>
                                    </tooltip>
                                </div>
                                <div class="worker-phone">
                                    {{user.phone}}
                                </div>
                                <span class="i-wrapper" @click="openAddWorker(user)">
                                    <span class="i i-pencil2"></span>
                                    <span class="i i-pencil2-active"></span>
                                </span>
                            </div>
                        </draggable>
                        <div class="workers-list" v-else-if="group.users.length !== 0">
                            <div :id="'user_'+user.id" class="worker" v-for="user in group.users" :key="user.id">
                                <tooltip
                                    v-if="!user.banned"
                                    :direction="'bottom-left-direction'"
                                    :add-class-wrap="'accesses'"
                                    :add-style="{width: 230 + 'px'}">
                                    <span slot="tooltipBtn">
                                        <div class="num">
                                            {{Object.keys(user.rights).length}}
                                        </div>
                                    </span>

                                    <div v-if="Object.keys(user.rights).length>0" slot="tooltipContent">
                                        <template v-for="sect in topSections">
                                            <div v-if="user.rights[sect.id]" class="flex-row" :key="user.id+'_'+sect.id" >
                                                <div class="text-bold text-size-s">
                                                    {{sect.name}}
                                                </div>
                                                <div class="access" :class="{editor : user.rights[sect.id]=='W', reader : user.rights[sect.id]=='R'}">
                                                    {{(user.rights[sect.id]=='W')?'редактор':'читатель'}}
                                                </div>
                                            </div>
                                        </template>
                                    </div>
                                </tooltip>
                                <div class="i i-stop i--red" v-else></div>
                                <div class="worker-name">
                                    <span class="text-size-m">{{user.name}}</span>
                                    <span class="text-color-grey text-size-xs">{{user.post}}</span>
                                </div>
                                <div class="worker-email">
                                    {{user.email}}
                                </div>
                                <div class="kick-sms">
                                    <tooltip
                                        v-if="user.mark>0 && !user.banned"
                                        :direction="'top-center-direction'"
                                        :isBtnClose="false"
                                        :add-style="{width: 184 + 'px'}">
                                        <span slot="tooltipBtn">
                                            <div class="notify-count" :class="{active: kickActive}">
                                                <span>{{user.mark}}</span>
                                            </div>
                                        </span>

                                        <div slot="tooltipContent">
                                            <div class="text-size-s mb8" >
                                                Непрочитаных правил: <span class="text-bold">{{user.mark}}</span>
                                            </div>
                                            <a href="javascript:void(0);" class="link-icon" @click="kickUnRead(user.id, $event)">
                                                <span class="standart">
                                                    <span class="i i-kick"></span>
                                                    <span class="text">пнуть по смс</span>
                                                </span>
                                                <span class="sended">
                                                    <span class="i i-ok7"></span>
                                                    <span class="text">смс отправлено</span>
                                                </span>
                                            </a>
                                        </div>
                                    </tooltip>
                                </div>
                                <div class="worker-phone">
                                    {{user.phone}}
                                </div>
                                <span class="i-wrapper" @click="openAddWorker(user)">
                                    <span class="i i-pencil2"></span>
                                    <span class="i i-pencil2-active"></span>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="footer-copyright">
                        © 1980-2092 Алексей Земсков
                    </div>
                </div>
            </div>
        </div>
        <popup :is-set-styled="false" :show="popups.staffGroup.show" :name="popups.staffGroup.name" :width="400" :add-class="'add-cat'">
            <staff-group></staff-group>
        </popup>

        <popup :is-set-styled="false" :is-btn-close="false" :show="popups.removeStaffGroupError.show" :name="popups.removeStaffGroupError.name" :width="400" >
            <remove-staff-group-error></remove-staff-group-error>
        </popup>

        <popup :is-set-styled="false" :show="popups.addWorker.show" :name="popups.addWorker.name" :width="488" :add-class="'add-worker'">
            <add-worker></add-worker>
        </popup>

        <popup :is-set-styled="false" :show="popups.removeWorker.show" :name="popups.removeWorker.name" :width="400" >
            <remove-worker></remove-worker>
        </popup>

        <popup :is-set-styled="false" :is-btn-close="false" :show="popups.kickUnReadAll.show" :name="popups.kickUnReadAll.name" :width="400" >
            <kick-un-read-all></kick-un-read-all>
        </popup>
    </div>
</template>


<script>
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import StaffGroup from '@/components/modal/rules/StaffGroup.vue';
    import RemoveStaffGroupError from '@/components/modal/rules/RemoveStaffGroupError.vue';
    import KickUnReadAll from '@/components/modal/rules/KickUnReadAll.vue';
    import AddWorker from '@/components/modal/rules/AddWorker.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import draggable from '@/helpers/vuedraggable.js';
    import RemoveWorker from '@/components/modal/rules/RemoveWorker.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import Tumbler from '@/components/Tumbler.vue';

    export default {
        metaInfo() {
            return {
                title: 'Сотрудники',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Staff",
        components: {
            Popup,
            StaffGroup,
            RemoveStaffGroupError,
            Tooltip,
            draggable,
            AddWorker,
            RemoveWorker,
            SubmitBtn,
            Tumbler,
            KickUnReadAll,
        },
        data: function(){
            return {
                drag: false,
                curDrugGroupIndex: 0,
                btnType: 'normal',
                kickUnReadAllBtnType: 'normal',
                kickActive: false,
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                usersGrouped: state => state.rulesStaff.usersByDepartmentList,
                topSections: state => state.rules.topSections,
                tumblers: state => state.rules.tumblers,
            }),
        },
        methods:{
            setValue(value, index){
                this.$store.commit('rulesStaff/setUsersByDepartmentListByIndex', {
                    items: value,
                    index: index
                });
            },
            up: function(index){
                this.$store.dispatch('rulesStaff/setMoveGroup', {index: index, direction: 'up'});
            },
            down: function(index){
                this.$store.dispatch('rulesStaff/setMoveGroup', {index: index, direction: 'down'});
            },
            openStaffGroup: function (group = []) {
                this.$store.commit('rulesStaff/setEditGroup', group);
                this.$store.dispatch('popups/open', {name: this.popups.staffGroup.name});
            },
            openAddWorker: function (user = []) {
                this.$store.commit('rulesStaff/setEditUser', user);
                this.$store.dispatch('popups/open', {name: this.popups.addWorker.name});
            },
            dragSave: function (index) {
                if (this.drag) {
                    this.$store.dispatch('rulesStaff/setUserSort', {index: index});
                    this.drag = false;
                }
            },
            dragStart: function (index) {
                this.curDrugGroupIndex = index;
            },
            dragStatus: function (e) {
                this.drag = true;
            },
            kickUnRead(id, e){
                e.target.closest('.link-icon').classList.add('load');
                setTimeout(() => {
                    e.target.closest('.link-icon').classList.remove('load');
                }, 5000);
                this.$store.commit('rulesStaff/kickUnReadBtnType', {id: id,type: 'load'});
                this.$store.dispatch('rulesStaff/kickUnRead', {id: id}).then(()=>{
                    this.$store.commit('rulesStaff/kickUnReadBtnType', {id: id,type: 'normal'});
                });
            },
            kickUnReadAll(){
                this.kickUnReadAllBtnType = 'load';
                this.$store.dispatch('rulesStaff/kickUnReadAll').then(()=>{
                    this.kickUnReadAllBtnType = 'normal';
                });
            },
            openKickUnReadAll(){
                this.$store.commit('popups/open', {name: this.popups.kickUnReadAll.name});
            }
        },
        mounted() {
            this.$store.dispatch('rulesStaff/getUsersByDepartment');
        },
    }
</script>
