<template>
    <div class="inp-s-wrap inp-select full" :class="[{ opened: focused, active: showLabel, error: isError, 'repeat-error':  repeatError }, addClass]"
         v-clickoutside="selectClose">
        <div class="select-overlay" @click="selectToggle"></div>
        <span class="inp-s-label" v-if="!keywords" :class="{error: isError, 'repeat-error':  repeatError }">{{placeholder}}</span>
        <div v-if="tag==='div'" class="inp-s" :class="{error:isError, 'repeat-error':  repeatError }" v-html="keywords">

        </div>

        <input v-else :class="{error:isError, 'repeat-error':  repeatError }" type="text" v-model="keywords" name="" class="inp-s" :disabled="disabled">


        <div class="select-arrow"></div>
        <div class="select-list" :style="listStyle">
            <ul v-if="focus">
                <li v-for="(res,index) in options" :key="index" class="fs-item"
                    @click="change(index)" :class="{active: index === choosen}">
                    <span class="i i-ok2" v-if="keywords == res.name"></span>
                    <span v-html="res.name"></span>
                </li>
            </ul>
            <ul class="select-list --ghost" v-show="false">
                <li>
                    <div></div>
                </li>
                <li>
                    <div></div>
                </li>
                <li>
                    <div></div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'InputSelectNew',
        components: {},
        props: {
            tag: {default: ''},
            addClass: {default: ''},
            isError: {default: false},
            listStyle: {default: ''},
            options: {
                default() {
                    return [];
                }
            },
            value: {default: ''},
            placeholder: {default: ''},
            valueIndex: {default: ''},
            modelId: {default: 0},
            disabled: {default: true}
        },
        model: {
            prop: 'modelId',
            event: 'change'
        },
        data: function () {
            return {
                keywords: '', //Слежение за инпутом
                resopened: false, //Отвечает за видимость списка результатов быстрого поиска
                choosen: null,
                focus: false,
                repeatError: false,
            }
        },
        mounted() {
            if(this.value !==''){
                let option = this.options.find(option => option.id == this.value);
                this.keywords = option ? option.name : '';
            }
            else if(this.modelId !== null){
                let option = this.options.find(option => option.id == this.modelId);
                this.keywords = option ? option.name : '';
            }
            if(this.valueIndex != ''){
                this.change(this.valueIndex * 1);
            }
        },
        watch:{
            keywords(){
                this.$emit('input',this.keywords);
            },
            valueIndex(){
                if(this.valueIndex != ''){
                    this.change(this.valueIndex * 1);
                }else{
                    this.keywords = '';
                    this.choosen = null;
                }
            },
            isError(val,oldVal){
                if(oldVal){
                    this.repeatError = true;
                }
            },
            modelId(after){
                let option = this.options.find(option => option.id == after);
                this.keywords = option ? option.name : '';
            }
        },
        computed: {
            focused() {
                return this.focus
            },
            showLabel() {
                return this.keywords.toString().length > 0;
            },
        },
        methods: {
            selectToggle(){
                this.focus = !this.focus;
                this.$emit('open', this.focus);
            },
            change: function(index){
                //this.keywords = this.options[index].name;
                this.focus = false;
                this.repeatError = false;
                this.choosen = index;
                this.$emit('change', this.options[index].id);
            },
            selectClose: function () {
                this.focus = false;
            },
        },
        directives: {
            'clickoutside': {
                bind: function (el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) {
                            warn += `Found in component '${compName}'`
                        }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function (el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>

