<template>
    <div>
      <div class="popup-info popup-icon-info">
          <svg class="big-icon" style="width: 48px; height: 48px;">
            <use xlink:href="@/assets/img/big-icons-sprite.svg#cat"></use>
          </svg>

          <div class="text mt24">
              <div class="text-size-l text-bold info-title text-color-red2">Удалить сотрудника?</div>

              <p class="info-text text-color-red2">
                  Это действие нельзя отменить
              </p>
          </div>

          <div class="mt24">
             <submit-btn @click.native="userDelete" :type="btnType"><span class="i mr8" :class="{'i-ok7': btnType=='normal'}"></span>Подтвердить</submit-btn>
          </div>
      </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';

    export default {
        name: "RemoveWorker",
        methods: {
            close: function () {
                this.$store.commit('popups/close', {name: this.popups.removeWorker.name});
            },
            userDelete(){
                this.btnType = 'load';
                this.$store.dispatch('rulesStaff/userDelete', {id: this.editUser.id}).then(()=>{
                    this.btnType = 'normal';
                    setTimeout(()=>{
                        this.$store.commit('popups/close', {name: this.popups.removeWorker.name});
                        this.$store.commit('rulesStaff/setEditUser',[]);
                    }, 100);
                });
            }
        },
        data(){
           return {
               btnType: 'normal'
           }
        },
        components:{
            SubmitBtn,
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                editUser: state => state.rulesStaff.editUser,
            }),
        }
    }
</script>

